.UserDialog-Wrapper{
  /*全屏*/
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;

  /*内容居中*/
  display: flex;
  justify-content: center;
  align-items: center;

  /* 给dialog添加遮罩效果 */
  background: hsla(0,0%,0%,0.40);
}

.UserDialog{
  border: 1px solid;
  border-radius: 4px;
  min-width: 20em;
  background: white;
  border: 1px solid #aaa;
  box-shadow: 0 0 1em hsla(0, 0%, 0%, 0.35); /*阴影和灰度*/
}

.UserDialog .signInOrSignUp > nav:first-child{
  padding: 1em;
  margin-left: -2em;
}
.UserDialog .signInOrSignUp > nav:first-child label{
  margin-left: 2em;
  cursor: pointer;
}

.UserDialog .signInOrSignUp > .panes{
  padding: 0 1em 1em;
}

.UserDialog .forgotPassword > h3 {
  padding: 1em;
}
.UserDialog .forgotPassword > form {
  padding: 0 1em 1em;
}

.UserDialog form>.row{
  display: flex;
  padding: .3em 0;
}
.UserDialog form>.row>label:first-child{
  width: 4em;
}

/* 添加「忘记密码了？」按钮 */
.UserDialog form>.row.actions{
  padding-left: 4em;
  display: flex;
  align-items: center;
}
.UserDialog form>.row.actions button{
  margin-right: .5em;
}
.UserDialog button{
  font: inherit;
  padding: .2em;
}

